// eslint-disable-next-line
<template>
  <div class="modal-wrapper" @click="onClose">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <div class="modal-title"><b>이용안내</b></div>
          <button type="button" class="close">
            <span @click="onClose">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <ul class="basicInfo">
            <p style="font-size: 1.1rem; margin-bottom: 1%">기본정보</p>
            <li>
              {{ details.type }}
              <a v-show="details.type == office">(일별계산)</a>
              <a v-show="details.type != office">(시간별계산)</a>
            </li>
            <li>{{ details.spaceId }}(인원)</li>
            <li>
              {{ details.info }}
            </li>
          </ul>
          <hr />
          <ul class="basicInfo">
            편의시설
            <li>
              {{ details.location }}
            </li>
          </ul>
          <hr />
          <ul class="basicInfo">
            결제정보
            <li v-show="details.type == office">
              후불결제 : 예약등록한 카드로 마지막 이용일에 결제 처리가 됩니다.
            </li>
            <li v-show="details.type != office">
              선결제: 결제 금액의 5%가 적립 
              <li v-show="details.type != office">보증금 결제: 전체 금액의 20%만 결제
              하되 이용 후 마이페이지에서 남은 금액을 결제 가능.</li>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    details: {
      type: Object,
      required: true,
    },
  },
  setup(props, { emit }) {
    const onClose = () => {
      emit("close");
    };

    return {
      onClose,
    };
  },
};
</script>

<style scoped>
.modal-wrapper {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-title {
  padding-left: 5%;
  font-size: 1.5rem;
}
.modal-header {
  height: 70px;
}
.basicInfo {
  padding-left: 5%;
  opacity: 0.5;
}
.modal-content {
  height: fit-content;
  /* text-align: center; */
}
.close {
  margin: 0;
}
</style>
